@font-face {
  font-family: "Roboto Condensed", sans-serif;
  font-weight: 400;
  font-style: normal;
  font-display: swap;
  src: url("../fonts/roboto_condensed_regular.woff2") format("woff2"), url("../fonts/roboto_condensed_regular.woff") format("woff");
}

@font-face {
  font-family: "Roboto Condensed", sans-serif;
  font-weight: 700;
  font-style: normal;
  font-display: swap;
  src: url("../fonts/roboto_condensed_bold.woff2") format("woff2"), url("../fonts/roboto_condensed_bold.woff") format("woff");
}


@font-face {
  font-family: "PT Mono", monospace;
  font-weight: 400;
  font-style: normal;
  font-display: swap;
  src: url("../fonts/pt_mono_regular.woff2") format("woff2"), url("../fonts/pt_mono_regular.woff") format("woff");
}

* {
  margin: 0;
  padding: 0;
}

html,
body {
  height: 100%;
}

body {
  display: flex;
  flex-direction: column;
  height: 100%;
}

main {
  flex: 1 0 auto;
}

footer {
  flex: 0 0 auto;
}
